import Selection from '../selection';
/**
 * @class API
 * Provides with methods working with Selection
 */
export default class SelectionAPI extends Module {
    /**
     * Save Editor config. API provides passed configuration to the Blocks
     */
    constructor({ config }) {
        super({ config });
    }
    /**
     * Available methods
     * @return {ISelectionAPI}
     */
    get methods() {
        return {
            findParentTag: (tagName, className) => this.findParentTag(tagName, className),
            expandToTag: (node) => this.expandToTag(node),
        };
    }
    /**
     * Looks ahead from selection and find passed tag with class name
     * @param {string} tagName - tag to find
     * @param {string} className - tag's class name
     * @return {HTMLElement|null}
     */
    findParentTag(tagName, className) {
        return new Selection().findParentTag(tagName, className);
    }
    /**
     * Expand selection to passed tag
     * @param {HTMLElement} node - tag that should contain selection
     */
    expandToTag(node) {
        new Selection().expandToTag(node);
    }
}
