/**
 * @class SaverAPI
 * provides with methods to save data
 */
export default class SaverAPI extends Module {
    /**
     * Save Editor config. API provides passed configuration to the Blocks
     */
    constructor({ config }) {
        super({ config });
    }
    /**
     * Available methods
     * @return {ISaverAPI}
     */
    get methods() {
        return {
            save: () => this.save(),
        };
    }
    /**
     * Return Editor's data
     */
    save() {
        return this.Editor.Saver.save();
    }
}
